export const selectRandomColor = () => {
  return colord(initial[Math.floor(Math.random() * initial.length)]);
};

export default function Dashboard() {
  const theme = useTheme();
  const { mode } = theme.palette;

  useEffect(() => {
    document.body.setAttribute(
      "style",
      mode === "light" ? "background: #f2f2f2;" : "background: #262626;"
    );
  }, [mode]);

  const [selectedCol, setSelectedCol] = useState<Colord>(selectRandomColor());
  const [open, setOpen] = useState(false);
  const [showText, setShowText] = useState(false);

  const isReallySmallScreen = useReallySmallScreen();

  const colorMode = useContext(ColorModeContext);

  const handleClose = (
    event: React.SyntheticEvent | Event,
    reason?: string
  ) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const action = (
    <Fragment>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </Fragment>
  );

  return (
    <Box
      sx={{
        overflowX: "hidden",
        color: "text.primary",
        transition: "all 0.25s ease-in-out",
        height: "100%",
        pb: 4,
      }}
    >
      <Header
        setSelectedColor={setSelectedCol}
        mode={mode}
        toggleClick={colorMode.toggleColorMode}
        showText={showText}
        setShowText={setShowText}
      />
      <ColorViewer
        selectedCol={selectedCol}
        setOpen={setOpen}
        showText={showText}
      />
      <Box
        sx={{
          p: isReallySmallScreen ? 1 : 3,
        }}
      >
        <ColorDashboard
          selectedColor={selectedCol}
          setSelectedColor={setSelectedCol}
          setOpen={setOpen}
        />
        <Snackbar
          open={open}
          autoHideDuration={6000}
          onClose={handleClose}
          message="Color copied to clipboard! "
          action={action}
        />
      </Box>
    </Box>
  );
}
